@import url('./__title/place-form__title.css');
@import url('./__title-input/place-form__title-input.css');
@import url('./__url-input/place-form__url-input.css');
@import url('./__save-btn/place-form__save-btn.css');

.place-form {
  background-color: white;
  color: black;
  max-width: 450px;
  display: flex;
  flex-flow: column;
  z-index: 2;
  border-radius: 10px;
  position: relative;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.15);
  padding: 36px;
}

@media screen and (max-width: 725px) {
  .place-form {
    padding: 30px 20px;
    max-height: 258px;
  }
}
