@import url('./__name/form__name.css');
@import url('./__profession/form__profession.css');
@import url('./__save-btn/form__save-btn.css');
@import url('./__title/form__title.css');

.form {
  background-color: white;
  color: black;
  max-width: 450px;
  display: flex;
  flex-flow: column;
  z-index: 2;
  border-radius: 10px;
  position: relative;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.15);
  padding: 36px;
}

@media screen and (max-width: 725px) {
  .form {
    padding: 30px 20px;
    max-height: 258px;
  }
}
