@import url('./__logo/header__logo.css');

.header {
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  max-height: 119px;
  padding-top: 45px;
  align-self: stretch;
  max-width: 882px;
  margin: 0 auto;
}

@media screen and (max-width: 900px) {
  .header {
    padding: 28px 0 0 10%;
    max-height: 56px;
  }
}

@media screen and (max-width: 320px) {
  .header {
    padding: 28px 0 0 27px;
  }
}
